.pong-container {
  position: relative;
  width: 100%;
  max-width: 600px;
  height: 100%;
  max-height: 900px;
  margin: 0 auto;
  padding: 1rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}

canvas {
  border: 8px solid #58ecec;
  display: block;
  margin: 0 auto;
  max-width: 100%;
  height: auto;
}

/* Position arrows inside the canvas with transparency */
.control-icons-container {
  position: absolute;
  width: 100%;
  max-width: 600px;
  display: flex;
  justify-content: space-between;
  gap: 15px;
}

.control-icon {
  font-size: 2rem;
  cursor: pointer;
  color: #0095DD;
  opacity: 0.7; /* 70% opacity */
  user-select: none;
  padding: 0.5rem;
}

/* Positioning for top and bottom controls */
.control-icons-top {
  top: 10px; /* Adjust for desired spacing from the top */
}

.control-icons-bottom {
  bottom: 10px; /* Adjust for desired spacing from the bottom */
}
 