/* CoverPage.css */

.cover-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  height: 100vh;
  padding: 2rem 1rem;
  background-color: #222d43; /* Matches the Telegram-friendly dark theme */
  color: #fff;
}

.cover-page h1 {
  font-size: 2em;
  margin: 0.5em 0;
}

.cover-page p {
  font-size: 1.2em;
  margin: 0.5em 0;
}

.mode-buttons {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 300px; /* Ensures buttons donâ€™t stretch too wide on large screens */
  margin-top: 1em;
}

.mode-button {
  background-color: #0088cc; /* Telegram blue */
  color: white;
  font-size: 1.2em;
  padding: 1em;
  margin: 0.5em 0;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 100%;
}

.mode-button:hover {
  background-color: #007bbd;
}

.mode-button.disabled {
  background-color: #555;
  color: #ccc;
  cursor: not-allowed;
}

/* Responsive adjustments */
@media (max-width: 600px) {
  .cover-page h1 {
    font-size: 1.8em;
  }
  .mode-button {
    font-size: 1em;
    padding: 0.8em;
  }
}
